import React, { useState, useEffect } from 'react';
import {
  Box,
  Button,
  Container,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
  IconButton,
  CircularProgress,
  MenuItem,
  Select,
  FormControl,
  InputLabel,
  Alert
} from '@mui/material';
import axios from 'axios';
import Swal from "sweetalert2";
import HomeIcon from '@mui/icons-material/Home';
import FlightIcon from '@mui/icons-material/Flight';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import CloudDownloadIcon from '@mui/icons-material/CloudDownload';
import DownloadIcon from '@mui/icons-material/Download';
import LocalShippingIcon from '@mui/icons-material/LocalShipping';

import DeleteIcon from '@mui/icons-material/Delete';
import { useNavigate } from 'react-router-dom';
import LogoutIcon from '@mui/icons-material/Logout';

const TDKShippingService = () => {
  //const url = 'https://tdk-crm-dev.ukwest.cloudapp.azure.com'
  const url = 'https://talentrail.tdkglobal.com'
  //const url = 'http://localhost:8083'
  const [data, setData] = useState([]);
  const [carriers, setCarriers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [file, setFile] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);
        const response = await fetch(url+'/shipapi/data');
        const result = await response.json();
        setData(result);
        setLoading(false);
      } catch (error) {
        console.error('Error fetching data:', error);
        setLoading(false);
      }
    };

    const fetchCarriers = async () => {
      setLoading(true);
      const response2 = await fetch(url+"/shipapi/carrier/all");
      const reselt2 = await response2.json()
      console.log(reselt2)
      setCarriers(reselt2);
      setLoading(false);
  }

    fetchData();
    fetchCarriers();
  }, []);

  const fetchData2 = async () => {
    try {
      setLoading(true);
      const response = await fetch(url+'/shipapi/data');
      const result = await response.json();
      setData(result);
      setLoading(false);
    } catch (error) {
      console.error('Error fetching data:', error);
      setLoading(false);
    }
  };

  const navigate = useNavigate();

  const createLabel = async (i) => {
    await axios.post(url+'/shipapi/create-label?id='+i._id+"&carrier="+i.carrier,{id: i._id, carrier: i.carrier});
    fetchData2();
  }

  const deletLabel = async (i) => {
    Swal.fire({
      title: `Do you want to delete Order #${i?.orderId}`,
      showDenyButton: false,
      showCancelButton: true,
      confirmButtonText: "Confirm",
    }).then(async (result) => {
      if (result.isConfirmed) {
        axios.delete(url+'/shipapi/delete?id='+i._id).then(res2 => {
          fetchData2();
        });
       
      }})
    
  }

const handleLogout = () => {
    localStorage.removeItem('isAuthenticated');
    navigate('/');
};

  const handleFileChange = (event) => {
    setFile(event.target.files[0]);
  };

  const downloadExcel = async () => {
    try {
      const response = await fetch(url+'/shipapi/download-excel');
      
      if (!response.ok) {
        const error = await response.json();
        throw new Error(error.error || 'Download failed');
      }
      
      const blob = await response.blob();
      const downloadUrl = window.URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = downloadUrl;
      a.download = 'product-template.xlsx';
      document.body.appendChild(a);
      a.click();
      window.URL.revokeObjectURL(downloadUrl);
      document.body.removeChild(a);
    } catch (error) {
      console.error('Download failed:', error);
      alert('Error: ' + (error.message || 'Failed to download Excel file'));
    }
  };
  
  
  const handleServiceChange = async (index, event) => {
    const newCarrier = event.target.value;
    
    setData(prevData => {
      const newData = [...prevData];
      newData[index] = {
        ...newData[index],
        carrier: newCarrier,
        rateLoading: true
      };
      return newData;
    });
  
    try {
      const response = await axios.post(
        url + '/shipapi/get-rate?id=' + data[index]._id + "&carrier=" + newCarrier,
        {}
      );
      
      setData(prevData => {
        const newData = [...prevData];
        newData[index] = {
          ...newData[index],
          rate: response.data?.[0]?.shipmentCost,
          rateLoading: false
        };
        return newData;
      });
    } catch (error) {
      // Handle error case
      setData(prevData => {
        const newData = [...prevData];
        newData[index] = {
          ...newData[index],
          rateLoading: false,
          rate: undefined
        };
        return newData;
      });
      alert("Unable to fetch rates for this Carrier");
    }
  };

  const handleServiceChange2 = async (index, event) => {
    const newdata = [...data];
    newdata[index].carrier =  event.target.value
    //newdata[index].rateLoading =  true;
    //console.log('newdata[index]')
    setData(newdata);
    axios.post(url+'/shipapi/get-rate?id='+newdata[index]._id+"&carrier="+newdata[index].carrier,{})
    .then(res=> {
      
      newdata[index].rateLoading = false;
      
      newdata[index].rate =  res.data?.[0]?.shipmentCost;
      console.log('newdata[index]', newdata[index])
      setData(newdata);
    })
    // .catch(error => {
    //   alert("Unable to fetch rates for this Carrier")
    //   newdata[index].rateLoading = false;
    //   delete newdata[index].rate;
    //   setData(newdata);
    // });
    
  
  }

  const uploadExcel = async () => {
    if (!file) {
      alert('Please select a file first');
      return;
    }

    try {
      const formData = new FormData();
      formData.append('excelFile', file);

      const response = await fetch(url+'/shipapi/upload-excel', {
        method: 'POST',
        body: formData
      });

      const result = await response.json();
      
      if (!response.ok) throw new Error(result.error || 'Upload failed');
      
      alert('File uploaded successfully');
      window.location.reload();
    } catch (error) {
      console.error('Upload error:', error);
      alert('Error: ' + (error.message || 'Failed to upload file'));
    }
  };

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Paper elevation={3} sx={{ p: 4 }}>
        <Typography variant="h4" component="h1" align="center" gutterBottom sx={{ color: 'primary.main', mb: 4 }}>
          TDK Shipping Service
        </Typography>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
  <Typography variant="h4" component="h1" gutterBottom sx={{ color: 'primary.main', mb: 4 }}>
    TDK Shipping Service
  </Typography>
  <Button 
    variant="outlined" 
    startIcon={<LogoutIcon />}
    onClick={handleLogout}
    sx={{ mb: 4 }}
  >
    Logout
  </Button>
</Box>
        
        <Box sx={{ display: 'flex', gap: 2, mb: 4 }}>
          <Button
            variant="contained"
            startIcon={<CloudDownloadIcon />}
            onClick={downloadExcel}
          >
            Download Excel
          </Button>
          
          <Button
            variant="contained"
            component="label"
            startIcon={<CloudUploadIcon />}
          >
            Upload Excel
            <input
              type="file"
              hidden
              accept=".xlsx, .xls"
              onChange={handleFileChange}
            />
          </Button>
          
          {file && (
            <Button
              variant="contained"
              color="secondary"
              onClick={uploadExcel}
            >
              Submit Upload
            </Button>
          )}
        </Box>

        {loading ? (
          <Box display="flex" justifyContent="center" py={4}>
            <CircularProgress />
          </Box>
        ) : (
          <TableContainer component={Paper}>
            <Table>
              <TableHead>
                <TableRow sx={{ backgroundColor: 'grey.100' }}>
                  <TableCell>Order ID</TableCell>
                  <TableCell>Customer</TableCell>
                  <TableCell>Shipment ID</TableCell>
                  <TableCell>Shipment Date</TableCell>
                  <TableCell>Tracking ID</TableCell>
                  <TableCell>Carrier Service</TableCell>
                  <TableCell>Action</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {data.map((item, index) => (
                  <TableRow key={index} sx={{ '&:nth-of-type(even)': { backgroundColor: 'grey.50' } }}>
                    <TableCell>{item.orderId}</TableCell>
                    <TableCell>{item.address?.name +' '+ item.address?.cityStateZip || 'N/A'}</TableCell>
                    <TableCell>{item.labelData?.shipmentId || 'N/A'}</TableCell>
                    <TableCell>{item.labelData?.shipDate || 'N/A'}</TableCell>
                    <TableCell>{item.labelData?.trackingNumber || 'N/A'}</TableCell>
                    <TableCell>
                        <FormControl fullWidth size="small">
                          <Select
                            value={item.carrier || ''}
                           onChange={(e) => handleServiceChange(index, e)}
                            //displayEmpty
                          >
                            {carriers.map((service) => (
                              <MenuItem key={service.key} value={service.key}>
                                <Box sx={{ display: 'flex', alignItems: 'center' }}>
                                  {service.service.domestic ? (
                                    <HomeIcon color="primary" sx={{ mr: 1 }} />
                                  ) : (
                                    <FlightIcon color="secondary" sx={{ mr: 1 }} />
                                  )}
                                  <Box>
                                    <Typography variant="body2" sx={{ fontWeight: 'bold' }}>
                                      {service.name}
                                    </Typography>
                                    <Typography variant="caption" display="block">
                                      {service.service.name}
                                    </Typography>
                                    <Typography variant="caption" display="block">
                                      Balance: {service.balance.toFixed(2)}
                                    </Typography>
                                  </Box>
                                </Box>
                              </MenuItem>
                            ))}
                          </Select>
                        </FormControl>
                      </TableCell>
                      {item?.rateLoading && 
                      <TableCell><CircularProgress /></TableCell>
            
                      }
                     {!item?.rateLoading && <TableCell>
                     
                      {item?.rate && <h5>Cost : ${item?.rate}</h5>}
                      {item?.carrier && item?.labelData && <IconButton
                        component="a"
                        href={`data:application/pdf;base64,${item.labelData?.labelData}`}
                        download={`label_${item.labelData?.shipmentId || 'unknown'}.pdf`}
                        color="primary"
                      >
                        <DownloadIcon />
                      </IconButton>}

                      {item?.carrier &&  !item?.labelData && <IconButton
                        //component="a"
                        //href={`data:application/pdf;base64,${item.labelData?.labelData}`}
                        //download={`label_${item.labelData?.shipmentId || 'unknown'}.pdf`}
                        color="primary"
                      >
                        <LocalShippingIcon onClick={()=>createLabel(item)} />
                      </IconButton>}
                       <IconButton
                        //component="a"
                        //href={`data:application/pdf;base64,${item.labelData?.labelData}`}
                        //download={`label_${item.labelData?.shipmentId || 'unknown'}.pdf`}
                        color="red"
                      >
                        <DeleteIcon onClick={()=>deletLabel(item)} color="red" />
                      </IconButton>
                      
                    </TableCell>}
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        )}
      </Paper>
    </Container>
  );
};

export default TDKShippingService;